<template>
  <v-layout>
    <v-container>
      <add-user v-on:getVal="getVal" />
      <v-data-table
        :loading="loadingTable"
        :headers="headers"
        :items="users"
        class="elevation-1"
        hide-default-footer
        loading-text="Loading...  Please Wait For Get User "
      >
        <template v-slot:item.action="{ item }">
          <v-row justify-star>
            <v-col>
              <edit-user :item="item" v-on:getVal="getVal" />
            </v-col>
            <v-col>
              <delete-user v-on:getVal="getVal" :item="item" />
            </v-col>
          </v-row>
        </template>
        <template v-slot:item.role_id="{ item }">
          <v-chip :class="getColor(item.role_id)">
            {{ item.role.name }}
          </v-chip>
        </template>
        <!-- build pagination for table  -->
      </v-data-table>
      <v-pagination
        color="primary"
        v-model="page"
        :total-visible="7"
        :length="pageCount"
      ></v-pagination>
    </v-container>
  </v-layout>
</template>

<script>
import { userController } from "../../components/user/user";
export default {
  mounted() {
    this.getUser(1);
  },
  data() {
    return {
      loadingTable: null,
      headers: [
        {
          text: "Email",
          align: "start",
          sortable: false,
          value: "email",
        },
        { text: "User Name ", value: "name" },
        { text: "Role", value: "role_id" },
        { text: "Action", value: "action" },
      ],
      users: [],
      page: 0,
      pageCount: 0,
    };
  },
  watch: {
    page(page) {
      this.getUser(page);
    },
  },
  methods: {
    getVal() {
      this.users = [];
      this.getUser();
    },

    getUser: async function (page) {
      this.loadingTable = true;
      var data = await userController.getUsers(page);
      this.loadingTable = null;
      this.users = data.data;
      this.pageCount = data.pageCount;
      this.page = data.page;
    },
    editItem(item) {
      console.log(item);
    },

    getColor(item) {
      // 1 is admin
      // 2 is donations_manager
      // 3 informative
      //  anther is user ...
      if (item == 1) {
        return "primary";
      } else if (item == 2) {
        return "secondary";
      } else if (item == 3) {
        return "red";
      } else {
        return "";
      }
    },
  },
  components: {
    "edit-user": () => import("../../components/user/edit.vue"),
    "delete-user": () => import("../../components/user/delete.vue"),
    "add-user": () => import("../../components/user/add.vue"),
  },
};
</script>
