
import axios from "axios";
import store from '../../store/index'
export const userController = {

    addUser: async function (user) {
        var state = false;
        var mainUrl = store.state.mainUrl;
        var formData = new FormData();
        console.log(user.name);
        formData.append("name", user.name);
        formData.append("email", user.email);
        formData.append("role_id", user.role_id);
        formData.append("password", user.password);
        await axios
            .request({
                method: "POST",
                url: mainUrl + "/users",
                data: formData,
                headers: this.configAxios(),
            }).then(res => {
                // console.log('printing in add user function ');
                console.log(res.data);
                var status = !!(res.data.errors);
                if (status) {
                    state = false;
                    this.ShowAlert('red', 'An Error ' + res.data.errors.email);
                } else {
                    state = true;
                    this.ShowAlert('secondary', 'success add user ')
                }
            }).catch(error => {
                state = false;
                store.dispatch('showAlert', {
                    color: 'red', message: 'An Error ðŸ˜¥ please contact Us '
                        + error
                })
            })
        return state;
    },

    deleteUser: async function (id) {
        var state = false;
        var mainUrl = store.state.mainUrl;
        await axios
            .request({
                url: mainUrl + "/users/" + id,
                method: "DELETE",
                headers: this.configAxios(),
            }).then(res => {
                if (res.status == 200) {
                    state = true;
                    this.ShowAlert('secondary', 'done delete success');
                }
            }).catch(error => {
                state = false
                this.ShowAlert('red', 'An Error ERROR CODE' + error);
            }
            );
        return state;
    },
    getUsers: async function (page) {
        var mainUrl = store.state.mainUrl;
        var data = {
            data: [],
            page: 0,
            pageCount: 0,
        };
        await axios.request({
            url: mainUrl + "/users?page=" + page,
            method: "GET",
            headers: this.configAxios(),
        })
            .then(response => {
                if (response.status == 200) {
                    var resData = response.data.data;
                    data.page = resData.current_page;
                    data.pageCount = resData.last_page;
                    data.data = response.data.data.data;
                }
            })
            .catch(function (error) {
                store.dispatch('showAlert', {
                    color: 'red', message: 'An Error ðŸ˜¥ please contact Us '
                        + error
                })
            }
            );
        return data;
    },
    getRoles: async function () {
        var data = [];
        var mainUrl = store.state.mainUrl;
        await axios.request({
            url: mainUrl + "/roles",
            method: "GET",
            headers: this.configAxios(),
        }).then(res => {
            data = res.data.data;
        });
        return data;
    },

    updateUser: async function (user) {
        console.log(' change new user  ');

        var state = false;
        var mainUrl = store.state.mainUrl;
        var formData = new FormData();
        formData.append("name", user.name);
        formData.append("email", user.email);
        formData.append("role_id", user.role_id);
        if (user.password) {
            console.log('password change new ');
            formData.append("password", user.password);
        }
        await axios 
            .request({
                method: "POST",
                url: mainUrl + "/users/" + user.id,
                data: formData,
                headers: this.configAxios(),
            }).then(res => {
                if (res.status == 200) {
                    this.ShowAlert('secondary', 'done update success  ');
                }
                state = true;
            }).catch(error => {
                state = false;
                this.ShowAlert('red', 'An Error ðŸ˜¥ please contact Us '
                    + error);
            })

        return state;
    },


    configAxios() {
        var token = store.state.token;
        return {
            "Content-type": "multipart/form-data",
            "Content-Disposition": "form-data",
            Authorization: "Bearer " + token,
        }
    },
    ShowAlert: function (color, message) {
        store.dispatch('showAlert', {
            color: color, message: message
        })
    },
}